import appShop from "../../components/page-component/app-shop/app-shop.vue";

export default {
    name: "store",
    components: {appShop},
    data() {
        return {
            getFocus: false,
            longitude: 0,
            latitude: 0,
            url: '',
            keyword: '',
            goods_id: 0,
            list: null,
            args: false,
            load: false,
            page: 1,
            toSearch: false,
        }
    },

    onLoad(options) { this.$commonLoad.onload(options);
        const self = this;
        if (options.book_id) {
            self.url = self.$api.book.store_list;
            self.goods_id = options.book_id
        } else {
            self.url = self.$api.store.list;
            self.goods_id = 0
        }

        //
        // self.longitude = '1';
        // self.latitude = '1';
        // self.loadData();
        // return;
        this.t(() => {

            uni.getLocation({
                success: function (res) {
                    self.longitude = res.longitude;
                    self.latitude = res.latitude;
                    self.loadData();
                },
                fail: function () {
                    uni.showToast({
                        title: '请开启手机位置权限',
                        icon: 'none',
                        duration: 1000,
                        success: function () {

                        }
                    });
                },
            });

















        });

    },

    onPullDownRefresh: function () {
        const self = this;
        self.keyword = '';
        self.toSearch = false;
        self.page = 1;

        this.t(() => {

            uni.getLocation({
                success: function (res) {
                    self.longitude = res.longitude;
                    self.latitude = res.latitude;
                    self.loadData();
                },
                fail: function () {
                    uni.showToast({
                        title: '请开启手机位置权限',
                        icon: 'none',
                        duration: 1000,
                        success: function () {

                        }
                    });
                },
                complete: function () {
                    uni.stopPullDownRefresh();
                }
            });

















        });
    },

    onShareAppMessage() {
        return this.$shareAppMessage({
            title: this.$children[0].navigationBarTitle,
            path: '/pages/store/store',
            params: {}
        });
    },

    onReachBottom: function () {
            const self = this;
            if (self.args || self.load)
                return;
            self.load = true;
            let page = self.page + 1;

            self.$request({
                url: self.url,
                data: {
                    page: page,
                    longitude: self.longitude,
                    latitude: self.latitude,
                    keyword: self.keyword,
                    goods_id: self.goods_id,
                }
            }).then(info => {
                if (info.code === 0) {
                    [self.page, self.args, self.list] = [page, info.data.list.length === 0, self.list.concat(self.forMatter(info.data.list))];
                }
                self.load = false;
            });
        },

        methods: {
            t(func) {





                const scope = 'scope.userLocation';
                uni.authorize({
                    scope,
                    success(res) {
                        func();
                    },
                    fail(e) {
                        uni.showModal({
                            title: '提示',
                            content: '您好,请先授权地理位置权限',
                            showCancel: false,
                            success(res) {
                                if (res.confirm) {
                                    uni.openSetting({
                                        success(settingdata) {
                                            if (settingdata.authSetting[scope]) {
                                                func();
                                            }
                                        }
                                    })
                                }
                            }
                        })
                    }
                });

            },
            /*
             * update
             */
            forMatter(list) {
                list.forEach((v, k) => {
                    v.pic_url = v.cover_url;
                    let array = [];
                    for (let i = 0; i < v.score; i++) {
                        array.push(i);
                    }
                    v.score = array
                })
                return list;
            },
            //搜索
            bindConfirm: function (e) {
                this.loadData();
            },

            beSearch: function (e) {
                if (this.keyword.length === 0) {
                    this.toSearch = !this.toSearch;
                    this.getFocus = this.toSearch;
                }
            },

            cancelPrint() {
                this.keyword = '';
                this.toSearch = false;
                this.loadData();
            },

            bindInput: function (e) {
                this.keyword = e.detail.value;
            },

            loadData: function () {
                const self = this;

                self.$showLoading();
                self.$request({
                    url: self.url,
                    data: {
                        keyword: self.keyword,
                        longitude: self.longitude,
                        latitude: self.latitude,
                        goods_id: self.goods_id
                    },
                }).then(info => {
                    self.$hideLoading();
                    if (info.code === 0) {
                        self.list = self.forMatter(info.data.list);
                    }
                }).catch(() => {
                    self.$hideLoading();
                })
            },
        }
    }
